@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
}
.App
{
  display: -webkit-flex;
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
   padding:60px 30px 0px 30px ;
   -webkit-flex:1.5;
           flex:1.5;
   -webkit-flex:var(--sidebar-flex);
           flex:var(--sidebar-flex); 
   background:black; 
}
.condiv
{
  padding: 100px ;
  background: white;
  -webkit-flex:6;
          flex:6;
  -webkit-flex:var(--condiv-flex);
          flex:var(--condiv-flex);
}

.about
{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  
}
.profilepic
{
  border-radius: 25%;
  width: 400px;
  margin: 30px;
}
/* .typingeffect
{
  font-size: var(--typing-font);
  margin-bottom: 30px;
} */

.projects{
  padding: 100px ;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  background: white;
}
nav ul{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: white !important;
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: white;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
  text-decoration: underline !important;
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.social
{
  position: absolute;
  margin-top: 20px;
  display: -webkit-flex;
  display: flex;
  bottom: 60px;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}
.social i:hover
{
  color: #ffffff;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: -webkit-flex;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}

.skills ul{
  text-align:center;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
  font-weight: bold;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  /* .condiv
  {
    padding: 50px ;
  } */
  nav ul
  {
    display: -webkit-flex;
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }
}

 .card-header {
    font-family: 'Caveat', cursive;
    color: white;
    text-align: center;
    font-size: 30px;
   }

   .card {
    background-image:url(/kimt-portfolio/static/media/galaxy.47e92abc.jpg);
    border-radius: 5px;
    margin: 1rem;
    position: relative;
    width: 325px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: show;
    text-align: center;
  }
  .card > .img-container > img {
    width: 100%
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
  }
  
  .card > .content > ul {
    list-style-type: none;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
    color: white;
  }
  button{
    background-color:#41004D;
    color:lavender;
  }
  
  button:hover{
    font-weight: Bold;
  }
.wrapper {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: wrap;
            flex-flow: wrap;
    padding: 20px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    margin-right: 10px;
  }

  h1 {
      -webkit-align-items: center;
              align-items: center;
  }
